.page-main {
  max-width: 100%;
  overflow-x: hidden;
}

.page-main__title-block {
  padding-top: 27px;
  margin-bottom: 38px;

  @media (min-width: @tablet-width) {
    padding-top: 58px;
    margin-bottom: 56px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 21px;
  }
}

.page-main__title {
  margin: 0;
}

.page-main__title-block + .page-main__catalog {
  margin-top: 41px;

  @media (min-width: @tablet-width) {
    margin-top: 83px;
  }
}

.page-main__catalog {
  margin-left: -20px;
  margin-right: -20px;

  @media (min-width: @tablet-width) {
    margin: 0;
  }
}

.page-main__form {
  margin-left: -20px;
  margin-right: -20px;

  @media (min-width: @tablet-width) {
    margin: 0;
  }
}

.page-main__add-products {
  margin-top: 28px;
  padding-bottom: 35px;

  @media (min-width: @tablet-width) {
    margin-top: 68px;
    padding-bottom: 76px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 75px;
    padding-bottom: 86px;
  }
}
